import React, { useCallback } from 'react'
import {api} from '../api'
const ToQuestion = () => {

    const handleClick = useCallback(() => {
        async function datas () {
            const title = document.querySelector('#title')
            const description = document.querySelector('#description')
            const url = `${api}/perguntar`

            await fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({title: title.value, description: description.value})
            })

            title.value = ''
            description.value = ''

        }

        datas()
    }, [])
    return (
        <section className='container animeLeft'>
            <div className='card'>
                <div className='card-header'>
                    <h3>Realizar pergunta</h3>
                </div>
                <div className='card-body'>
                    <div className='form-group'>
                        {/* <label htmlFor='title'>Título</label> */}
                        <input className='form-control' id='title' placeholder='Título' />
                    </div>
                    <div className='form-group'>
                        {/* <label htmlFor='description'>Descrição</label> */}
                        <input className='form-control' id='description' placeholder='Descrição' />
                    </div>
                    <button className='btn btn-primary' onClick={handleClick}>Perguntar</button>
                </div>
            </div>
        </section>
    )
}

export default ToQuestion