import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import {api} from '../api'

const Question = () => {
    const {id} = useParams()
    const [question, setQuestion] = useState(null)
    const [anwsers, setAnwsers] = useState(null)
    // const [error, setError] = useState('')
    // const [load, setLoad] = useState('')
    const [update, setUpdate] = React.useState(0)

    useEffect(() => {
        async function datas () {
            const url = `${api}/pergunta/`
            const response = await fetch(url+id)
            const json = await response.json()
            setQuestion(json.question)
            setAnwsers(json.anwsers)
        }
        datas()
    }, [id, update])

    function handleClick (){
        async function datas () {
            const anwser = document.querySelector('#anwser')
            const url = `${api}/resposta`
            await fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },

                body: JSON.stringify({id_question: id, anwser: anwser.value})
            })
            anwser.value = ''
            setUpdate(update+1)
        }
        datas()
    
    }

    if(question == null) return null
    return (
        <section className='container animeLeft'>
            <div className='question mb-4'>
                {
                    question.map((question) => 
                    <div key={question.id}>
                            <h4>{question.title}</h4>
                            <hr />
                            <p>{question.description}</p>
                    </div>
                )}
            </div>
            <div>
                <h4>Responda</h4>
                <label htmlFor='anwser'>Resposta: </label>
                <textarea className='form-control' id='anwser' rows='4' />
                <button className='btn btn-primary my-3' onClick={handleClick}>Responder</button>
            </div>
            <div className='anwsers'>
                <hr />
                <h3>Respostas</h3>
                <hr />
                {anwsers && (
                    anwsers.map((anwser) => 
                        <div className='card mb-3' key={anwser.id}>
                            <div className='card-body'>
                                <p className='card-title rounded-sm'>{anwser.anwser}</p>
                            </div>
                        </div>
                    )
                )}
                
            </div>
        </section>
    )
}

export default Question